export const TRANSFER_STUDENT = gql `
    mutation transferStudent ($activity: Int, $classes: Int, $classtime: Int, $description: String, $docDate: String, $docNum: String, $program: Int, $school: Int, 
                                $section: Int, $status: Int, $statusExtra: Int, $student: Int) {
        transferStudent (activity: $activity, classes: $classes, classtime: $classtime, description: $description, docDate: $docDate, docNum: $docNum, program: $program, 
                        school: $school, section: $section, status: $status, statusExtra: $statusExtra, student: $student) {
            transfer {
                id
            }
        }
    }
`;

export const UPDATE_STUDENT = gql `
    mutation updateStudent (
        $address: String
        $birthCity: Int
        $birthDistrict: Int
        $birthdate: String
        $degree: Int
        $email: String
        $familyName: String
        $id: ID
        $joinBefore: String
        $joinDate: String
        $joinSchoolyear: String
        $name: String
        $phone: String
        $phone2: String
        $registerNo: String
        $sex: String
        $studentCode: String
        $username: String
    ) {
        updateStudent (
            address: $address
            birthCity: $birthCity
            birthDistrict: $birthDistrict
            birthdate: $birthdate
            degree: $degree
            email: $email
            familyName: $familyName
            id: $id
            joinBefore: $joinBefore
            joinDate: $joinDate
            joinSchoolyear: $joinSchoolyear
            name: $name
            phone: $phone
            phone2: $phone2
            registerNo: $registerNo
            sex: $sex
            studentCode: $studentCode
            username: $username
        ) {
            student {
                id
            }
        }    
    }
`;

